/**
 * /180
 * @type {number}
 */
export const convDegRad = Math.PI / 180
/**
 * 180/
 * @type {number}
 */
export const convRadDeg = 180 / Math.PI
/**
 * Valeur de cos(30)
 * @type {number}
 */
export const cos30 = Math.cos(30 * convDegRad)
/**
 * Valeur de sin(30)
 * @type {number}
 */
export const sin30 = 0.5
