'use strict'

const loadDelay = 60 // en s

// On charge le mme MathJax que mathgraph32
const mathJaxUrl = 'https://www.mathgraph32.org/js/MathJax3/es5/tex-svg.js'

/* la config, on avait en v2 le js
https://www.mathgraph32.org/ftp/js/MathJax/MathJax.js?config=TeX-AMS-MML_SVG-full
avec la config :
 MathJax.Hub.Config({
  tex2jax: {
    inlineMath: [
      ['$', '$'],
      ['\\(', '\\)']
    ]
  },
  SVG: {
    mtextFontInherit: false
  },
  jax: ['input/TeX', 'output/SVG'],
  TeX: {
    extensions: ['color.js']
  },
  messageStyle: 'none'
})
qui donne, via https://mathjax.github.io/MathJax-demos-web/convert-configuration/convert-configuration.html
cette conf v3
 */
const mathJaxConfig = {
  tex: {
    inlineMath: [
      ['$', '$'],
      ['\\(', '\\)']
    ],
    // packages: {'[+]': ['noerrors', 'color']}
    packages: { '[+]': ['color'] }
  },
  svg: {
    // cf http://docs.mathjax.org/en/latest/options/output/svg.html#the-configuration-block
    mtextInheritFont: true
  },
  options: {
    ignoreHtmlClass: 'tex2jax_ignore',
    processHtmlClass: 'tex2jax_process'
  },
  loader: {
    // la conversion donnait a
    // load: ['input/tex', 'output/svg']
    // mais a marchait pas, c'est bcp mieux avec
    load: ['[tex]/noerrors', '[tex]/color']
  }
}

// si on fait deux appels trs rapprochs  loadMathJax,
// on peut se retrouver dans le cas o le 2e retourne Promise.resolve() alors que MathJax n'est pas encore ready
let mathJaxLoadingPromise

/**
 * Charge mathjax dans le <head> de la page courante
 * (appel par le prepare de ActionEcrireTexte et ActionNommerPoint,
 * donc dclenche le chargement ds que le tableau des actions est construit,
 * seulement si l'une des actions a besoin de Mathjax)
 * @return {Promise}
 */
export default function loadMathJax () {
  // si on a dj t appel y'a rien  faire (c'est peut-tre encore en cours ou dj rsolu)
  if (mathJaxLoadingPromise) return mathJaxLoadingPromise

  // faut le charger
  mathJaxLoadingPromise = new Promise((resolve, reject) => {
    // si qqun d'autre a dj charg MathJax (iep dans un nud j3p prcdent par ex),
    // pas la peine de le refaire si c'est la bonne version
    if (typeof MathJax === 'object' && /^3\./.test(MathJax.version)) {
      // on force notre config, au cas o qqun l'aurait configur diffremment
      // cf http://docs.mathjax.org/en/latest/web/configuration.html#configuring-mathjax-after-it-is-loaded
      MathJax.config = mathJaxConfig
      // c'est tout bon
      return resolve()
    }

    // faut charger MathJax
    window.MathJax = mathJaxConfig
    /* global MathJax */
    // on ajoute la rsolution de la promesse quand il sera prt
    // cf http://docs.mathjax.org/en/latest/web/configuration.html#performing-actions-during-startup
    MathJax.startup = {
      ready: () => {
        // on vire le timeout
        clearTimeout(timeout)
        MathJax.startup.defaultReady()
        MathJax.startup.promise.then(() => {
          // on rsoud la promesse que retourne loadMathJax maintenant que MathJax est ready
          resolve()
        })
      } // ready
    } // window.MathJax.startup

    // chargement mathjax
    const eltScript = document.createElement('script')
    eltScript.type = 'text/javascript'
    // sinon ff rle avec du :
    // La demande daccs aux cookies ou au stockage sur  https://cdn.jsdelivr.net/npm/mathjax@3/es5/input/tex/extensions/noerrors.js  a t bloque parce que nous bloquons toutes les demandes daccs au stockage par des sites tiers et que le blocage de contenu est activ.
    // ce qui est curieux car il n'y a aucune demande
    eltScript.crossOrigin = 'anonymous'
    const url = window.mathJax3BaseUrl
      ? `${window.mathJax3BaseUrl}es5/tex-svg.js`
      : mathJaxUrl
    eltScript.src = url
    const head = document.getElementsByTagName('head')[0]
    head.appendChild(eltScript)
    // on ajoute un timeout de chargement
    const timeout = setTimeout(() => {
      reject(Error(`Mathjax non charg aprs ${loadDelay}s dattente (${url})`))
    }, loadDelay * 1000)
  })

  return mathJaxLoadingPromise
} // loadMathJax
